import React, { Component } from 'react';
import {Link,IndexLink} from 'react-router';
import './font/iconfont.css';
import './App.css';

//
class App extends Component {
  render() {
    return (
      <div className="App">
        <div className="Header"></div>
        {this.props.children}
        <div className="Footer">
              <IndexLink to="/" activeClassName="active"><i className="iconfont icon-fangzi1"></i></IndexLink>
              <Link to="/user" activeClassName="active"><i className="iconfont icon-fangdajing"></i></Link>
              
              <Link to="/login" activeClassName="active"><i className="iconfont icon-gerenzhongxinxia-copy"></i></Link>
        </div>
      </div>
    );
  }
}

export default App;



