import React, { Component } from 'react';
import {Link,IndexLink} from 'react-router';
import ClickBack from '../components/ClickBack';
import './Deliver.css';

//
class Deliverlist extends Component {
  render() {
    return (
      <div className="Deliverlist">
        <div className="Header">
           <ClickBack title={""}/>
        </div>
    
        <div className="Content">
            <div className="tab">
              <IndexLink to="all" activeClassName="active" className="tab_b"></IndexLink>
              <Link to="face" activeClassName="active" className="tab_b"></Link>
              
              <Link to="unsuit" activeClassName="active" className="tab_b"></Link>
            </div>
        
        <ul class="list">
        {this.props.children}
        </ul>
        
      </div>
      </div>
    );
  }
}

export default Deliverlist;


